import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AirlineService } from './../../services/airline.service';


@Component({
  selector: 'app-edit-airline',
  templateUrl: './edit-airline.component.html',
  styleUrls: ['./edit-airline.component.css']
})
export class EditAirlineComponent implements OnInit {

  airline = {};

  constructor(private airlineService: AirlineService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.getAirline(this.route.snapshot.params['id']);
  }

  getAirline(id) {
    this.airlineService.showAirline(id).then((res) => {
      this.airline = res;
      console.log(this.airline);
    }, (err) => {
      console.log(err);
    });
  }

  updateAirline(id) {
    this.airlineService.updateAirline(id, this.airline).then((result) => {
      let id = result['_id'];
      this.router.navigate(['/details', id]);
    }, (err) => {
      console.log(err);
    });
  }

}



