import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { AirlineService } from './../../services/airline.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Response } from '@angular/http';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class AdminComponent implements OnInit {

  AirlineList: any;

  constructor(private route: ActivatedRoute, private router: Router,private airlineService: AirlineService) { }

  ngOnInit() {
    this.getAirlineList();
  }

  getAirlineList() {
    this.airlineService.getAllAirlines().then((res) => {
      this.AirlineList = res;
      console.log(this.AirlineList);
    }, (err) => {
      console.log(err);
    });
  }
    deleteAirline(id) {
    this.airlineService.deleteAirline(id).then((result) => {
      this.router.navigate(['/home']);
    }, (err) => {
      console.log(err);
    });
  }

}



