import { Injectable } from '@angular/core';
import { AuthService } from './auth.service';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Observable } from "RxJS/Rx";
import 'rxjs/add/operator/map';

@Injectable()
export class AirlineService {

  options;
  domain = this.authService.domain;

  constructor(
    private authService: AuthService,
    private http: Http
  ) { }

  // Function to create headers, add token, to be used in HTTP requests
  createAuthenticationHeaders() {
    this.authService.loadToken(); // Get token so it can be attached to headers
    // Headers configuration options
    this.options = new RequestOptions({
      headers: new Headers({
        'Content-Type': 'application/json', // Format set to JSON
        'authorization': this.authService.authToken // Attach token
      })
    });
  }
  
  getAirlineList() {
    this.createAuthenticationHeaders(); // Create headers
        return this.http.get(this.domain + 'api', this.options);
    }

    getAllAirlines() {
       this.createAuthenticationHeaders(); // Create headers
    return new Promise((resolve, reject) => {
      this.http.get(this.domain + 'airline', this.options)
        .map(res => res.json())
        .subscribe(res => {
          resolve(res);
        }, (err) => {
          reject(err);
        });
    });
  }

  showAirline(id) {
     this.createAuthenticationHeaders(); // Create headers
    return new Promise((resolve, reject) => {
        this.http.get(this.domain +'airline/' + id, this.options)
          .map(res => res.json())
          .subscribe(res => {
            resolve(res)
        }, (err) => {
          reject(err);
        });
    });
  }

  saveAirline(data) {
     this.createAuthenticationHeaders(); // Create headers
    return new Promise((resolve, reject) => {
        this.http.post(this.domain +'airline', data, this.options)
          .map(res => res.json())
          .subscribe(res => {
            resolve(res);
          }, (err) => {
            reject(err);
          });
    });
  }

  updateAirline(id, data) {
     this.createAuthenticationHeaders(); // Create headers
    return new Promise((resolve, reject) => {
        this.http.put(this.domain +'airline/'+id, data, this.options)
          .map(res => res.json())
          .subscribe(res => {
            resolve(res);
          }, (err) => {
            reject(err);
          });
    });
  }

  deleteAirline(id) {
     this.createAuthenticationHeaders(); // Create headers
    return new Promise((resolve, reject) => {
        this.http.delete(this.domain +'airline/'+id, this.options)
          .subscribe(res => {
            resolve(res);
          }, (err) => {
            reject(err);
          });
    });
  }
  
 
  postReview(id, comment, rating, reviewer ) {
    this.createAuthenticationHeaders(); // Create headers
    // Create reviewData to pass to backend
    const reviewData = {
      id: id,
      comment: comment,
      rating: rating,
      reviewer : reviewer
    }
    return this.http.post(this.domain + 'airline/review', reviewData, this.options).map(res => res.json());

  }

}



