import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AirlineService } from './../../services/airline.service';

@Component({
  selector: 'app-view-airline',
  templateUrl: './view-airline.component.html',
  styleUrls: ['./view-airline.component.css']
})
export class ViewAirlineComponent implements OnInit {
airline = {};
  constructor(private route: ActivatedRoute, private router: Router, private airlineService: AirlineService) { }

  ngOnInit() {
    this.getAirlineDetail(this.route.snapshot.params['id']);
  }

  getAirlineDetail(id) {
    this.airlineService.showAirline(id).then((res) => {
      this.airline = res;
      console.log(this.airline);
    }, (err) => {
      console.log(err);
    });
  }

  deleteAirline(id) {
    this.airlineService.deleteAirline(id).then((result) => {
      this.ngOnInit();
    }, (err) => {
      console.log(err);
    });
  }


}



