import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AirlineService } from './../../services/airline.service';

@Component({
  selector: 'app-new-airline',
  templateUrl: './new-airline.component.html',
  styleUrls: ['./new-airline.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class NewAirlineComponent implements OnInit {

 airline = {};

  constructor(private airlineService: AirlineService, private router: Router) { }

  ngOnInit() {
  }

  saveAirline() {
    this.airlineService.saveAirline(this.airline).then((result) => {
      let id = result['_id'];
      this.router.navigate(['/details', id]);
    }, (err) => {
      console.log(err);
    });
  }


}



