
import { AuthService } from '../../services/auth.service';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AirlineService } from './../../services/airline.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Response } from '@angular/http';



@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class HomeComponent implements OnInit {

  AirlineList: any;
  messageClass;
  message;  
  loadingBlogs = false;  
  commentForm;
  processing = false;
  username;  
  newComment = [];
  enabledComments = [];

  constructor(
    private route: ActivatedRoute, 
    private router: Router,
    private airlineService: AirlineService, 
    private formBuilder: FormBuilder,
    private authService: AuthService) { 
    this.createCommentForm(); // Create form for posting comments on a user's blog post
  }

  ngOnInit() {
    // Get profile username on page load
    this.authService.getProfile().subscribe(profile => {
      this.username = profile.user.username; // Used when creating new blog posts and comments
    });
    this.getAirlineList();
  }
  // Create form for posting comments
  createCommentForm() {
    this.commentForm = this.formBuilder.group({
      comment: ['', Validators.compose([
        Validators.required,
        Validators.minLength(1),
        Validators.maxLength(200)
      ])],
      rating: ['']
    })
  }
     ratingComponetClick(clickObj: any): void {
       this.commentForm.get('rating').setValue(clickObj.rating); 
           
    }
  // Enable the comment form
  enableCommentForm() {
    this.commentForm.get('comment').enable(); // Enable comment field
    this.commentForm.get('rating').enable(); 
  }

  // Disable the comment form
  disableCommentForm() {
    this.commentForm.get('comment').disable(); // Disable comment field
     this.commentForm.get('rating').disable(); 
  }
  getAirlineList() {
    this.airlineService.getAllAirlines().then((res) => {
      this.AirlineList = res;
      console.log(this.AirlineList);
    }, (err) => {
      console.log(err);
    });
  }
  // Reload blogs on current page
  reloadBlogs() {
    this.loadingBlogs = true; // Used to lock button
    this.getAirlineList(); // Add any new blogs to the page
    setTimeout(() => {
      this.loadingBlogs = false; // Release button lock after four seconds
    }, 4000);
  }

  // Function to post a new comment on blog post
  draftComment(id) {
    this.commentForm.reset(); // Reset the comment form each time users starts a new comment
    this.newComment = []; // Clear array so only one post can be commented on at a time
    this.newComment.push(id); // Add the post that is being commented on to the array
  } 
  // Function to go back to previous page
  goBack() {
    window.location.reload(); // Clear all variable states
  }

 

  // Function to post a new comment
  postReview(id) {
    this.disableCommentForm(); // Disable form while saving comment to database
    this.processing = true; // Lock buttons while saving comment to database
    const comment = this.commentForm.get('comment').value; // Get the comment value to pass to service function
    const rating = this.commentForm.get('rating').value;
    // Function to save the comment to the database
    this.airlineService.postReview(id, comment, rating, this.username).subscribe(data => {
      this.getAirlineList(); // Refresh all blogs to reflect the new comment
      const index = this.newComment.indexOf(id); // Get the index of the blog id to remove from array
      this.newComment.splice(index, 1); // Remove id from the array
      this.enableCommentForm(); // Re-enable the form
      this.commentForm.reset(); // Reset the comment form
      this.processing = false; // Unlock buttons on comment form
      if (this.enabledComments.indexOf(id) < 0) this.expand(id); // Expand comments for user on comment submission
    });
  }

  // Expand the list of comments
  expand(id) {
    this.enabledComments.push(id); // Add the current blog post id to array
  }

  // Collapse the list of comments
  collapse(id) {
    const index = this.enabledComments.indexOf(id); // Get position of id in array
    this.enabledComments.splice(index, 1); // Remove id from array
  }
}



